<template>
  <div class="hot">
    <div class="hot-logo">
      <div class="logo"></div>
      <div class="times">更新日期：12月24日</div>
    </div>
    
    <HotMusic 
      @update:currentMusic="$emit('update:currentMusic',$event)"
      @update:playList="$emit('update:playList',$event)" 
      :hotSongs="hotSongs"
      :currentMusic="$attrs.currentMusic"
      :paused="$attrs.paused"
    />
  </div>
</template>

<script>
import HotMusic from "@/components/HotMusic.vue";

export default {
  components: {
    HotMusic,
  },

  data() {
    return {
      songs: null,
      hotSongs : null,
    }
  },
  created() {
    this.$root.isShowLoading = true;

    this.axios
      .get("/top/list?idx=1")
      .then((response) => {
        this.songs = response.data.playlist.trackIds.slice(0, 20).map((e) => e.id).join();

        this.$root.isShowLoading = true;

        this.axios.get("/song/detail?ids=" + this.songs).then((response) => {
            this.hotSongs = response.data.songs
            .map((item)=>{
              return {
                id: item.id,
                name: item.name,
                picUrl: item.al.picUrl,
                isShowHotIndex:true,
                song: {
                  artists:item.ar
                },
              }
            });
          })
          .finally(() => {
            this.$root.isShowLoading = false;
          });
      })
      .finally(() => {
        this.$root.isShowLoading = false;
      });
  },
};
</script>

