<template>
    <section>

        <ul :class="{paused:Boolean(currentMusic)}">
            <SongItem 
            @update:currentMusic="$emit('update:currentMusic',$event)" 
            @update:playList="$emit('update:playList',hotSongs)" 
            v-for="(song,index) in hotSongs" 
            :key="song.id" 
            :item="song" 
            :index="index" 
            :currentMusic="currentMusic"
            :paused="$attrs.paused"
            />
        </ul>
    </section>
</template>

<script>
import SongItem from "@/components/SongItem.vue";
export default {
    components: {
        SongItem
    }, 
    
    props:{
        hotSongs:{
            type:Array,
        },
        currentMusic:{
            type:Object,
        },
    },
};
</script>

<style>
    ul.paused{
        margin-bottom: 50px;
    }
</style>